import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import Background from '../../assets/background.png';

import Container from '../../components/login/Container';

import Form from '../../components/lib/Form';
import TextField from '../../components/lib/TextField';
import PrimaryButton from '../../components/lib/Button/PrimaryButton';
import CardPanel from '../../components/lib/Card';

import { notifySuccess, notifyError } from '../../actions/notification';
import { resetPassword } from '../../actions/api/auth';

const styles = {
  root: {
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center',
    backgroundSize: 'cover',
    backgroundImage: `url(${Background})`,
    flex: 1,
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  formTitle: {
    paddingLeft: '2.5%',
    paddingTop: '3%',
  },
  formInput: {
    paddingLeft: '5%',
    paddingRight: '5%',
    paddingBottom: '5%',
  },
  welcome: {
    marginBottom: 20,
    color: 'white',
    fontSize: 20,
  },
  title: {
    marginBottom: 60,
    color: 'white',
    fontSize: 50,
    fontWeight: 'bold',
    alignItems: 'felx-end',
  },
  logo: {
    color: '#ea5941',
    fontSize: 80,
    marginBottom: -13,
    marginRight: 10,
  },
};

class RecoverPass extends Component {
  constructor(props) {
    super(props);
    this.state = {
      password: '',
      redirect: false,
    };

    this.handlePassword = this.handlePassword.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentWillUnmount() {
    this.isUnmounted = true;
  }

  handlePassword(event) {
    const value = event;
    this.setState({
      password: value,
    });
  }

  handleSubmit(event) {
    event.preventDefault();
    const { password } = this.state;
    const { token } = this.props;
    if (password.length < 8) {
      this.props.notifyError('Password is too short (minimum is 8 characters)');
    } else {
      this.props.resetPassword(token, { password })
        .then((response) => {
          if (response.ok) {
            this.props.notifySuccess('Your password has changed succesfully');
            this.setState({ redirect: true });
          }
        });
    }
  }

  render() {
    const { password, redirect } = this.state;
    const { classes } = this.props;
    if (redirect) {
      return <Redirect to="/login" />;
    }
    return (
      <Container>
        <CardPanel>
          <Typography className={classes.formTitle} variant="headline" component="h2">

            Select new password
          </Typography>
          <Form
            onSubmit={this.handleSubmit}
            className={classes.formInput}
          >
            <TextField
              onChange={this.handlePassword}
              type="password"
              name={password}
              fullWidth
              label="Password"
              required
              id="password"
              autoFocus
            />
            <PrimaryButton
              type="submit"
            >

              Submit
            </PrimaryButton>
          </Form>
        </CardPanel>
      </Container>
    );
  }
}

RecoverPass.propTypes = {
  classes: PropTypes.any.isRequired,
  token: PropTypes.string.isRequired,
  resetPassword: PropTypes.func.isRequired,
  notifySuccess: PropTypes.func.isRequired,
  notifyError: PropTypes.func.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    token: String(ownProps.match.params.token),
  };
}

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    resetPassword,
    notifySuccess,
    notifyError,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(RecoverPass));
